import { useState } from 'react';
import { t } from '../../../helpers/i18n/dictionary';
import useAttribute from '../../../hooks/profiles/useAttribute';
import { AttributeVm } from '../../../hooks/profiles/useAttributes';
import ActionButton from '../../Buttons/ActionButton';
import SaveStatus from '../../Buttons/SaveStatus';
import ConfirmDialog from '../../Dialog/ConfirmDialog/ConfirmDialog';
import Section from '../../Sections/Section';
import AclEditor from '../../AclEditor/AclEditor';
import AttributeFields from '../AttributeFields/AttributeFields';

const AttributeEditor = ({
  attribute,
  className,
}: {
  attribute: AttributeVm;
  className?: string;
}) => {
  const [needsConfirmation, setNeedsConfirmation] = useState(false);
  const [, { mutate: saveAttr, status, isLoading, isError, isSuccess }, { mutate: removeAttr }] =
    useAttribute({});
  const [newAttr, setNewAttr] = useState({ ...attribute });

  const removeData = () => {
    setNeedsConfirmation(false);
    removeAttr(attribute);
  };

  const saveNewData = (dirtyAttr) => {
    saveAttr(dirtyAttr);
  };

  const changeHandler = (e: { target: { value: unknown; name: string } }) => {
    const dirtyAttr = { ...newAttr };

    dirtyAttr[e.target.name] = e.target.value;
    setNewAttr(dirtyAttr);

    saveNewData(dirtyAttr);
  };

  return (
    <>
      <Section
        isOpaqueBg={true}
        title={
          <>
            <AclEditor
              acl={newAttr.acl}
              onChange={(acl) => {
                changeHandler({ target: { name: 'acl', value: acl } });
              }}
            />{' '}
            {newAttr.typeDefinition.name}
          </>
        }
        className={`${className} relative`}
      >
        <AttributeFields attribute={newAttr} onChange={changeHandler} />
        <div className="top-5 right-5 flex flex-row md:absolute">
          <ActionButton
            type="remove"
            icon="trash"
            className="ml-auto"
            onClick={() => {
              setNeedsConfirmation(true);
            }}
          ></ActionButton>
          <ActionButton
            state={isLoading ? 'loading' : isSuccess ? 'success' : isError ? 'failed' : 'success'}
            type="primary"
            className="ml-2"
            onClick={() => saveNewData(newAttr)}
          >
            {t('Save')}
          </ActionButton>
        </div>
        <SaveStatus className="mt-2 text-right sm:mt-0" state={status} />
      </Section>
      <ConfirmDialog
        title="Remove Attribute"
        confirmText="Permanently remove"
        needConfirmation={needsConfirmation}
        onConfirm={removeData}
        onCancel={() => {
          setNeedsConfirmation(false);
        }}
      >
        <p className="text-sm text-gray-500">
          {t('Are you sure you want to remove your')} {attribute.typeDefinition.name}{' '}
          {t('attribute. This action cannot be undone.')}
        </p>
      </ConfirmDialog>
    </>
  );
};

export default AttributeEditor;
